// variables
$deepBlue:#032f3e;
$sectionHeading:24px;
$offWhite:#f8f9fb;

#main-nav{
    background: $deepBlue;
   
   li{
    float: left;
    width: 14%;

   
      }
   a{
    color:$offWhite ;
    text-decoration: none;
    padding: 16px;
    display: block;
    text-align: center;
}
}

#main-nav ul:after{
    content: "";
    display: block;
    clear: both;

}
section h1{
    font-size: $sectionHeading;
    color: $deepBlue;
}